// Import redux types
import { ActionCreator, Dispatch } from 'redux';
import { ThunkAction } from 'redux-thunk';
// import axios from 'axios';

// Import Question Typing
import { IQuestion, IQuestionGroup, IQuestionState } from './types';

import { AnswerActionTypes } from '../Answers/actions'
import { IAnswer, IAnswerState } from '../Answers/types';

///////////////////////////////////////////////////
// localStorage
import { SUPPORT_QUESTIONS } from './reducer';

import data from "./data.json"
const getFromInitial = () : IQuestionGroup[] =>  {
	return data.map(g => ({
		...g,
		questions: g.questions.map(q => ({ 
			...q,
			created: new Date(q.created)
		}))
	}));
}


// Create Action Constants
export enum QuestionActionTypes {
	GET_ALL_QUESTIONS = 'GET_ALL_QUESTIONS',
	GET_QUESTION = 'GET_QUESTION',
	ADD_QUESTION = 'ADD_QUESTION',
	EDIT_QUESTION = 'EDIT_QUESTION',
	REMOVE_QUESTION = 'REMOVE_QUESTION',
	STORE_QUESTION = 'STORE_QUESTION',
	CANCEL_QUESTION = 'CANCEL_QUESTION',
	// groups
	ADD_GROUP = 'ADD_GROUP',
	EDIT_GROUP = 'EDIT_GROUP',
	REMOVE_GROUP = 'REMOVE_GROUP',
	STORE_GROUP = 'STORE_GROUP',
	// question answers
	REMOVE_QUESTION_ANSWER = 'REMOVE_QUESTION_ANSWER',
	ASSIGN_QUESTION_ANSWER = 'ASSIGN_QUESTION_ANSWER',
	// localSTorage
	SET_IS_DETAIL = 'SET_IS_DETAIL'
}


// Interface for Get All Action Type
export interface IGetAll {
	type: QuestionActionTypes.GET_ALL_QUESTIONS;
	questionGroups: IQuestionGroup[];
}

export interface IGet {
	type: QuestionActionTypes.GET_QUESTION;
	questionId: number;
}

export interface IAdd {
	type: QuestionActionTypes.ADD_QUESTION;
	groupId: number
	//questionId: number,
}

export interface IEdit {
	type: QuestionActionTypes.EDIT_QUESTION;
	groupId: number,
	questionId: number
}

export interface IRemove {
	type: QuestionActionTypes.REMOVE_QUESTION;
	groupId: number,
	questionId: number,
}

export interface IStore {
	type: QuestionActionTypes.STORE_QUESTION;
	question: IQuestion;
}

export interface ICancel {
	type: QuestionActionTypes.CANCEL_QUESTION;
}

// group
export interface IAddGroup {
	type: QuestionActionTypes.ADD_GROUP;
	//groupId: number
}

export interface IEditGroup {
	type: QuestionActionTypes.EDIT_GROUP;
	groupId: number
}

export interface IRemoveGroup {
	type: QuestionActionTypes.REMOVE_GROUP;
	groupId: number
}

export interface IStoreGroup {
	type: QuestionActionTypes.STORE_GROUP;
	group: IQuestionGroup;
}


// question answers
export interface IRemoveQuestionAnswer {
	type: QuestionActionTypes.REMOVE_QUESTION_ANSWER;
	groupId: number,
	questionId: number,
	answerId: number
}

export interface IAssignQuestionAnswer {
	type: QuestionActionTypes.ASSIGN_QUESTION_ANSWER;
	groupId: number,
	questionId: number,
	answerId: number,
	tekst?: string
}

export interface ISetIsDetail {
	type: QuestionActionTypes.SET_IS_DETAIL;
	isDetail: boolean
}

export interface IStoreNewAnswer {
	type: AnswerActionTypes.STORE_ANSWER;
	answer: IAnswer;
}


// Combine the action types with a union (we assume there are more)
export type QuestionActions = IGetAll | IGet | IAdd | IEdit | IRemove | IStore | ICancel |
	IAddGroup | IEditGroup | IRemoveGroup | IStoreGroup |
	IRemoveQuestionAnswer | IAssignQuestionAnswer |
	ISetIsDetail | IStoreNewAnswer;

const isWebStorageSupported = () => 'localStorage' in window

export const storeNewAnswer: ActionCreator<
	ThunkAction<Promise<any>, IAnswerState, null, IAdd>
> = (answer: IAnswer, formMode: string) => {
	return async (dispatch: Dispatch) => {
		console.log('storeNewAnswer answer:', answer)
		try {
			if (formMode === 'add') {
				// const response = await axios.get('https://swapi.co/api/people/');
				dispatch({
					type: AnswerActionTypes.ADD_ANSWER
				});
				dispatch({
					type: AnswerActionTypes.STORE_ANSWER,
					answer
				});
			}
			else {
			}
			//if (isWebStorageSupported())
			// 	dispatch({type: AnswerActionTypes.STORE_ANSWERS_TO_LOCAL_STORAGE });		 
		} catch (err) {
			console.error(err);
		}
	};
};

// Get All Action <Promise<Return Type>, State Interface, Type of Param, Type of Action>
export const getAllQuestions: ActionCreator<
	ThunkAction<Promise<any>, IQuestionState, null, IGetAll>
> = () => {
	return async (dispatch: Dispatch) => {
		try {
			let questionGroups: IQuestionGroup[] = [];
			if (isWebStorageSupported()) {
				const sQuestions = localStorage.getItem(SUPPORT_QUESTIONS);
				console.log('sQuestions', sQuestions)
				if (sQuestions !== null) {
					console.log(111)
					questionGroups = JSON.parse(sQuestions)
				}
				else {
					console.log(222)
					questionGroups = getFromInitial();
				}
			}
			else {
				questionGroups = getFromInitial();
			}
			dispatch({
				type: QuestionActionTypes.GET_ALL_QUESTIONS,
				questionGroups
			});
		}
		catch (err) {
			console.error(err);
		}
	};
};


// Get Question <Promise<Return Type>, State Interface, Type of Param, Type of Action> 
export const getQuestion: ActionCreator<
	ThunkAction<Promise<any>, IQuestionState, string, IGet>
> = (questionId: number) => {
	return async (dispatch: Dispatch) => {
		try {
			dispatch({
				type: QuestionActionTypes.GET_QUESTION,
				questionId
			});
		} catch (err) {
			console.error(err);
		}
	};
};



