const updateGroupFromLocalStorage = (group: IQuestionGroup): Promise<any> => {
	return new Promise((resolve, reject) => {
		setTimeout(() => {
			resolve({
				'status': 200,
				'content-type': 'application/json',
				'data': {
					'results': group
				}
			})
		}, 50)
	})
}
