// Import Reducer type
import { Reducer } from 'redux';

import {
  QuestionActions,
  QuestionActionTypes
  //reloadQuestionsFromLocalStorage
} from './actions';

import { IQuestion, IQuestionGroup, IQuestionState } from './types'

export const SUPPORT_QUESTIONS = 'SUPPORT_QUESTIONS';

export const initialQuestion: IQuestion = {
	groupId: 0,
	questionId: 0,
	text: '',
	words: [],
	source: 0,
	status: 0,
	answers: [],
	createdBy: 0,
	created: new Date()
 };

 export const initialQuestionGroup: IQuestionGroup = {
	groupId: 0,
	title: '',
	questions: []
 };


// Define the initial state
export const initialQuestionState: IQuestionState = {
	questionGroups: [],
	question: undefined,
	loading: false,
	formMode: 'display',
	groupIdEditing: 0,
	isDetail: false
};


